

































import ut from '../../utils/index.js';
import Banner from '../../components/common/banner.vue'
import SearchBox from '../../components/common/searchBox.vue'
import ServerType from '../../components/common/serverType.vue'
export default {
	data() {
		return {
			static:"",
			swipeList: wx.getStorageSync('banderbuild')||[],
			newlist: [],
			recomlist: [],
			classlist:[],
			provinceName:'',
			countyAreaName:'',
			cityName:'通州'
		}
	},
	components:{
		Banner,
		SearchBox,
		ServerType
	},
	onLoad() {
		this.static=ut.static;
	},
	onShow() {
		const reload = ut.checkPageTime('index_build_time');
		if(!this.newlist.length||reload){
			this.req_new();
		}
		if(!this.recomlist.length||reload){
			this.req_recom();
		}
		if(!this.classlist.length||reload){
			this.req_class();
		}
		if(!this.swipeList.length||reload){
			this.req_banner();
		}
	},
	methods: {
		go_build_build2(_id,name){
			wx.navigateTo({
				url: `../build/build2?_id=${_id}&name=${name}`
			})
		},
		go_build_serch(){
			wx.navigateTo({
				url: '../build/serch'
			})
		},
		go_build_mallinf(_id,name){
			wx.navigateTo({
				url: `../build/mallinf?_id=${_id}&&title=${name}`
			})
		},
		req_class(){
			ut.request({
				data: {
					parentid:0
				},
				url: "goods/class"
			}).then(data=>{
				console.log(data)
				this.classlist=data;
			})
		},
		req_new(){
			ut.request({
				data: {
					parentid:0
				},
				url: "goods/newlist"
			}).then(data=>{
				this.newlist=data
			})
		},
		req_recom(){
			ut.request({
				data: {
					parentid:0
				},
				url: "goods/recommendlist"
			}).then(data=>{
				this.recomlist=data
			})
		},
		req_banner(){
			ut.request({
				allurl: ut.uploadimgurl+"common/banner",
				data:{type:1},
				method:'GET'
			}).then(data=>{
				this.swipeList=data;
				wx.setStorageSync('banderbuild',this.swipeList);
			})
		}
	}
}
